<template>
  <div class="modify-phone">
    <nav-head title="修改绑定手机"/>
    <div class="wrap">
      <input type="text" disabled v-bind:value="this.$route.params.phoneNum | phoneNumFormat">
      <input type="text" @input="handleInputNum" maxlength="11" v-model="newPhone" placeholder="请输入新手机号">
      <input type="text" minlength="6" maxlength="18" v-model="password" placeholder="请输入密码">
      <div class="relative">
        <input type="text" v-model="captcha" maxlength="6" placeholder="请输入短信验证码">
        <captcha-button :phone="newPhone" class="btn-captcha"></captcha-button>
      </div>
      <mt-button size="large" class="btn-modify" v-bind:disabled="!newPhone || !captcha || !password" @click.native="saveEdit" v-bind:class="{'display': newPhone && captcha && password}">确认修改</mt-button>
    </div>
  </div>
</template>
<script>
import NavHead from '@/components/NavHead.vue'
import CaptchaButton from '@/components/CaptchaButton'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { CHANGE_MOBILE } from '@/vuex/Mine'
import Util from '@/tool/util'

export default {
  name: 'modifyBindPhone',
  data () {
    return {
      newPhone: '',
      captcha: '',
      password: ''
    }
  },
  methods: {
    saveEdit () {
      if (this.newPhone && this.password && this.captcha) {
        if (!Util.isMobile(this.newPhone)) {
          Toast('请输入正确的手机号!')
          return
        }
        if (this.password.length < 6) {
          Toast('请输入6-18位密码!')
          return
        }
        this.changeMobile({
          mobile: this.newPhone,
          password: this.password,
          verification_code: this.captcha
        })
      }
    },
    handleInputNum (e) {
      this.newPhone = e.target.value.replace(/[^\d]/g, '')
    },
    ...mapActions({
      changeMobile: CHANGE_MOBILE
    })
  },
  components: {
    NavHead,
    CaptchaButton,
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  div.modify-phone {
    text-align: center;
    background-color: $background-light-color;
    width: 100%;
    position: absolute;
    min-height: 100%;
    > div.wrap {
      padding: 20px 25px 0 25px;
      input {
        border: none;
        outline: none;
        width: 100%;
        font-size: 16px;
        height: 56px;
        line-height: 56px;
        margin-bottom: 10px;
        background: none;
        border-bottom: 1px solid $border-color;
        border-radius: 0;
        color: $font-color;
      }
      .btn-captcha {
        position: absolute;
        margin-top: 5px;
        right: 0;
        top: 0;
      }
      .btn-modify {
        border: none;
        background-color: #ccc;
        color: white;
        box-shadow: none;
        height: 48px;
        font-size: 17px;
        margin-top: 20px;
      }
      .btn-modify.display{
        background-color: $back-color;
        @if $IF365 { color: #131313; }
      }
    }
  }
</style>
